import React, { Fragment } from 'react';
import { graphql, Link } from 'gatsby';



export default  ({data}) => (
    <>
      <div>about page</div>
      <Link to='/'>Back to home</Link>
      <h2>{data.site.siteMetadata.title}</h2>
      <div>{data.site.siteMetadata.description}</div>
    </>
)

export const query = graphql `
   query {
    site {
        siteMetadata {
          author
          description
          siteUrl
          title
        }
      }
    }
`
